.page-full{
  background: url("../../../images/ayrlomusic-background-full.jpg") no-repeat;
  background-size: cover;
  height: 100vh;
  display: flex;
  .page-group{
    //display: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height: 8rem;
    overflow: auto;

    img{
      align-content: space-between;
      margin-top: 1rem;
      margin-bottom: 1rem;
      border: 2px solid #E1E1E1;
    }
  }
}


@media (min-width: 768px){
  .page-full{
    background: url("../../../images/ayrlomusic-background-full.jpg") no-repeat;
    height: 100vh;
    background-size: cover;
    background-position-x: center;
    display: flex;
    justify-content: right;
    align-items: center;
    padding-right: 4rem;

    .page-group{
      display: flex;
      flex-wrap: wrap;
      flex-direction : row;
      overflow: auto;
      width: 16%;
      height: 50rem;
      margin-left: auto;
      padding-right: 0.8rem;
      padding-left: 0.8rem;

      img{
        align-content: space-between;
        margin-top: 1rem;
        margin-bottom: 1rem;
        border: 2px solid #E1E1E1;
      }
    }
  }
}


